

.formulario1 {

    margin: 20px;
    border: solid white 5px;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    padding: 40px;
    height: 100%;
    width: 100%;
    background: linear-gradient(180deg, rgb(99, 77, 226) 0%, rgba(4, 0, 249, 0.834) 100%);
    
    
    
}

.nome_input{
    font-size: 1em;
}

.inputs{
    display: block;
    height: auto;
    width: auto;
    outline: unset;
    border: 3px solid #ffffff;
    border-radius: 15px;

    
}

.text-center{
    margin-bottom: 5px;

}

.dados { 
    margin: 5px;
    width: 40px;
    height: 40px;
    border-radius: 25px;
    justify-content: center;

}

.arrumando{
    display: block;
    justify-content:center;
}

.botao_cadastro{
    margin-top: 10px;
}

@media(min-width:490px){
    .arrumando{
        justify-content: center;
        text-align: center;
        display:grid;
        margin-right: 100px;

       
    }
    .formulario1 {

        margin: 20px;
        border: solid white 5px;
        border-radius: 10px;
        justify-content: center;
        align-items: center;
        padding-right: 50px;
        height: 100%;
        width: 100%;
        background: linear-gradient(180deg, rgb(99, 77, 226) 0%, rgba(4, 0, 249, 0.834) 100%);
        
        
    }
}

