.start h1{
    font-size: 3.5em;
}

.start p{
    margin-bottom: 2em;
    color:#ecfa00;
}

@media(min-width:740px){
    .start h1{
        font-size: 3.5em;
    }
    
    .start p{
        font-size: 1.5em;
        margin-bottom: 2em;
        color:#ecfa00;
    }

    .botao_inicio{
    background: #1646a0;
    color: #fff;
    padding: 0px 50px;
    border-radius: 40px;
    height: 60px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1.2em;
    cursor: pointer;
    transition: 0.4;

    }
}

@media(max-width:341px){
    .start h1{
        font-size: 2.5em;
    }
    
    .start p{
        font-size: 0.8em;
        margin-bottom: 2em;
        color:#ecfa00;
    }

    .botao_inicio{
        background: #1646a0;
        color: #fff;
        padding: 0px 35px;
        border-radius: 25px;
        height: 35px;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 1.2em;
        cursor: pointer;
        transition: 0.4;
    
    }


}