html,body{
  height: 100%;
}

body{
  font-family: Helvetica;
  margin: 0;
  padding: 0;
  background: linear-gradient(180deg, rgb(99, 77, 226) 0%, rgba(4, 0, 249, 0.834) 100%);
  color: white;
}

button{
  background: #1646a0;
  color: #fff;
  padding: 0px 45px;
  border-radius: 25px;
  height: 50px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.2em;
  cursor: pointer;
  transition: 0.4;
}

button:hover{
  background-color: #0923af;
}

@media(max-width:750px){
  body{
    font-family: Helvetica;
    margin: 0;
    padding: 0;
    background:rgb(99, 77, 226);
    color: white;
  }
}