.jogo h1{
    font-size: 2.5em;
    
}

.pontos span{
    font-weight: bold;
}

.dica span{
    color: #ecfa00;
}

.palavraContainer{
    margin: 1.5em;
    padding: 1.5em;
    border: 20px solid #ecfa00;
    display: flex;
}

.letra, .quadrado_branco{
    font-size: 70px;
    line-height: 1.5;
    border: 3px solid #000;
    height: 100px;
    width: 100px;
    text-transform: uppercase;
    background-color: #fff;
    color: #000;
    font-weight: bold;

}

.letraContainer p {
    margin-bottom: 1.2em;
}

.letraContainer input{
    height: 50px;
    width: 50px;
    font-size: 2em;
    text-align: center;
    margin-right: 1em;
}

@media(max-width:1400px) and (min-width:750px){
    .letra, .quadrado_branco{
        font-size: 38px;
        line-height: 1.4;
        border: 3px solid #000;
        height: 50px;
        width: 50px;
        text-transform: uppercase;
        background-color: #fff;
        color: #000;
        font-weight: bold;
    
    }
    .letraUtilizadas{
        font-size: 1em;
    }
    .jogo h1{
        font-size: 2em;
        
    }
    .dica{
        font-size: 1.5em;
    }
    .palavraContainer{
        margin: 1.5em;
        padding: 2em;
        border: 20px solid #ecfa00;
        display: flex;
    }

}

@media(max-width: 750px) and (min-width:381px){
    .letra, .quadrado_branco{
        font-size: 20px;
        line-height: 1.3;
        border: 3px solid #000;
        height: 22px;
        width: 22px;
        text-transform: uppercase;
        background-color: #fff;
        color: #000;
        font-weight: bold;
    
    }
    .letraUtilizadas{
        font-size: 1em;
    }
    .jogo h1{
        font-size: 2em;
        
    }
    .dica{
        font-size: 1em;
    }
    .palavraContainer{
        margin: 1.5em;
        padding: 1.5em;
        border: 15px solid #ecfa00;
        display: flex;
    }
}

@media(max-width:380px){
    .letra, .quadrado_branco{
        font-size: 17px;
        line-height: 1.4;
        border: 3px solid #000;
        height: 18px;
        width: 18px;
        text-transform: uppercase;
        background-color: #fff;
        color: #000;
        font-weight: bold;
    
    }
    .letraUtilizadas{
        font-size: 1em;
    }
    .jogo h1{
        font-size: 1em;
        
    }
    .palavraContainer{
        margin: 1.5em;
        padding: 1em;
        border: 15px solid #ecfa00;
        display: flex;
        margin-left: 40px;
    }
    .dica{
        font-size: 0.8em;
    }
    
}




